<template>
<div class="sales-board">
      <div class="sales-board-intro">
        <h2>流量分析</h2>
        <p>是指在获得网站访问量基本数据的情况下对有关数据进行统计、分析，从中发现用户访问网站的规律，并将这些规律与网络营销策略等相结合，从而发现目前网络营销活动中可能存在的问题，并为进一步修正或重新制定网络营销策略提供依据。当然这样的定义是站在网络营销管理的角度来考虑的</p>
      </div>
      <div class="sales-board-form">
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  购买数量：
              </div>
              <div class="sales-board-line-right">
                <counter :min="countMin" :max="countMax" @on-change="onParamChange('buyNumber', $event)"></counter>
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  产品类型：
              </div>
              <div class="sales-board-line-right">
                  <selection :selections="buyTypes" @on-change="onParamChange('buyType', $event)"></selection>
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  有效时间：
              </div>
              <div class="sales-board-line-right">
                  <selection :selections="periodList" @on-change="onParamChange('buyTime', $event)"></selection>
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  产品版本：
              </div>
              <div class="sales-board-line-right">
                  <mul-chooser
                  :selections="versionList"
                  @on-change="onParamChange('buyVersion', $event)"></mul-chooser>
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  总价：
              </div>
              <div class="sales-board-line-right">
                  {{ price }} 元
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">&nbsp;</div>
              <div class="sales-board-line-right">
                  <div class="button" @click="showPayDialog">
                    立即购买
                  </div>
              </div>
          </div>
      </div>
      <div class="sales-board-des">
        <h2>产品说明</h2>
        <p>网站访问统计分析报告的基础数据源于网站流量统计信息，但其价值远高于原始数据资料。专业的网站访问统计分析报告对网络营销的价值，正如专业的财务分析报告对企业经营策略的价值。</p>

        <h3>用户行为指标</h3>
        <ul>
          <li>用户行为指标主要反映用户是如何来到网站的、在网站上停留了多长时间、访问了哪些页面等，主要的统计指标包括：</li>
          <li>用户在网站的停留时间；</li>
          <li>用户来源网站（也叫“引导网站”）；</li>
          <li>用户所使用的搜索引擎及其关键词；</li>
          <li>在不同时段的用户访问量情况等。</li>
        </ul>

        <h3>浏览网站方式</h3>
        <ul>
          <li>用户上网设备类型</li>
          <li>用户浏览器的名称和版本</li>
          <li>访问者电脑分辨率显示模式</li>
          <li>用户所使用的操作系统名称和版本</li>
          <li>用户所在地理区域分布状况等</li>
        </ul>
      </div>
      <my-dialog :isShow="dialogIsShow"
      	@no-close="hidePayDialog">
      	<table class="buy-dialog-table">
          <tr>
            <th>购买数量</th>
            <th>产品类型</th>
            <th>有效时间</th>
            <th>产品版本</th>
            <th>总价</th>
          </tr>
          <tr>
            <td>{{ buyNumber }}</td>
            <td>{{ buyTypes[buyType].label }}</td>
            <td>{{ periodList[buyTime].label }}</td>
            <td>
              <span v-for="(item, index) in versionList" v-show="getVersionInfo(index)"> {{ item.label }}</span>
            </td>
            <td>{{ price }}</td>
          </tr>
        </table>
        <h3 class="buy-dialog-title">请选择银行</h3>
        <chooser-bank @on-change="selectBank"></chooser-bank>
        <div class="button buy-dialog-btn" @click="confirmBuy">
          确认购买
        </div>
      </my-dialog>
      <my-dialog :isShow="bankInShow"
      	@no-close="bankIsHide">
      	<div>你选择了：购买数量为 {{buyNumber}},产品类型为 {{buyTypes[buyType].label}},有效时间为 {{periodList[buyTime].label}},版本为<span v-for="(item, index) in versionList" v-show="getVersionInfo(index)"> {{ item.label }}</span>,使用银行卡为 {{selectBank.label}}</div>
      </my-dialog>
  </div>
</template>

<script>
	import counter from '@/components/counter'
	import selection from '@/components/selection'
	import mulChooser from '@/components/multipyChooser'
	import dialog from '@/components/dialog'
	import chooserBank from '@/components/chooserBank'
	export default {
		name: 'detailAnalysis',
		data () {
			return {
				buyNumber: 1,
				buyType: 0,
				buyTime: 0,
				buyVersion: [0],
				price: 0,
				dialogIsShow: false,
				countMin: 1,
				countMax: 10,
				selectBank: {
          id: 201,
          label: '招商银行',
          name: 'zhaoshang'
		    },
				bankInShow: false,
	      buyTypes: [
	        {
	          label: '入门版',
	          value: 0
	        },
	        {
	          label: '中级版',
	          value: 1
	        },
	        {
	          label: '高级版',
	          value: 2
	        }
	      ],
	      periodList: [
	        {
	          label: '半年',
	          value: 0
	        },
	        {
	          label: '一年',
	          value: 1
	        },
	        {
	          label: '三年',
	          value: 2
	        }
	      ],
	      versionList: [
	        {
	          label: '客户版',
	          value: 0
	        },
	        {
	          label: '代理商版',
	          value: 1
	        },
	        {
	          label: '专家版',
	          value: 2
	        }
	      ]
			}	
		},
		components: {
			counter,
			selection,
			mulChooser,
			myDialog: dialog,
			chooserBank
		},
		computed: {
			
		},
		filters : {
			
		},
		methods: {
			onParamChange (arg, e) {
				this[arg] = e;
				console.log(e)
			},
			showPayDialog () {
				this.dialogIsShow = true
			},
			hidePayDialog () {
				this.dialogIsShow = false
			},
			getVersionInfo (index) {
				return this.buyVersion.includes(index)
			},
			selectBank (e) {
				this.selectBank = e
			},
			bankIsHide () {
				this.bankInShow = false; 
			},
			confirmBuy () {
				this.bankInShow = true; 
			}
		}
	}
</script>

<style scoped>
	.buy-dialog-title {
	  font-size: 16px;
	  font-weight: bold;
	}
	.buy-dialog-btn {
	  margin-top: 20px;
	}
	.buy-dialog-table {
	  width: 100%;
	  margin-bottom: 20px;
	}
	.buy-dialog-table td,
	.buy-dialog-table th{
	  border: 1px solid #e3e3e3;
	  text-align: center;
	  padding: 5px 0;
	}
	.buy-dialog-table th {
	  background: #4fc08d;
	  color: #fff;
	  border: 1px solid #4fc08d;
	}
</style>



